import { getComplexRandomID } from '../util'

const random = getComplexRandomID(32)
const CONST_LOCALSTORATE_STATELIST_KEY = `FormEditorStateList-${random}`
const CONST_LOCALSTORATE_STATEINDEX_KEY = `FormEditorStateIndex-${random}`

// console.info('USING RANDOM ID: ', random)
// console.log(`USING RANDOM ID: %c ${random}`, 'background: #ffffff; color: #f567aa')
console.log(`USING RANDOM ID: %c ${random}`, 'color: #f567aa')

window.CONST_LOCALSTORATE_STATELIST_KEY = CONST_LOCALSTORATE_STATELIST_KEY
window.CONST_LOCALSTORATE_STATEINDEX_KEY = CONST_LOCALSTORATE_STATEINDEX_KEY

export {CONST_LOCALSTORATE_STATELIST_KEY, CONST_LOCALSTORATE_STATEINDEX_KEY}



