<template draggable="false" ondragstart="javascript: return false;">
  <div class="fe-content-table-container" draggable="false" tabindex="600" @focus="handleFocus" @blur="handleBlur" ondragstart="javascript: return false;" :style="{width: totalWidth + 'px'}">
    <table id="fe-content-table-table" class="fe-content-table-table" cellspacing="0" draggable="false" ondragstart="javascript: return false;" :style="{
      width: getTableWidth() + 'px'
    }"
           @mousemove="handleMouseMove($event)"
           @mouseover="handleMouseOver($event)"
           @mousedown="handleMouseDown($event)"
           @mouseup="handleMouseUp($event)"
           @mouseleave="handleMouseLeave($event)"
           @click="handleClick($event)"
           @dblclick="handleMouseDblClick($event)"
           @contextmenu.prevent="showMenu($event)"
    >
      <!---->
      <tr @mouseleave="handleHeadMouseLeave" class="noselect" draggable="false" ondragstart="javascript: return false;">
        <!---->
        <td class="fe-content-table-table-th-0" draggable="false" ondragstart="javascript: return false;" table-selector
            :style="{
            height: defaultRowHeight + 'px',
            width: 46 + 'px',
            cursor: 'cell'
            }">
        </td>
        <td v-for="(headCell, col) in cells[0]" v-if="col !== 0" v-bind:key="'th' + col" class="fe-content-table-table-line0" :col="col" draggable="false" ondragstart="javascript: return false;" col-header
            :style="{
              height: defaultRowHeight + 'px',
              width: (getColWidth(col) || defaultColWidth) + 'px',
              boxSizing: 'border-box',
              background: col >= area.beginCell.col && col <= area.endCell.col ? '#abd5ff' : ''
            }"
        >
          <template v-if="type !== 'resize-col'">
            <div col-header-div :col="col" v-if="type !== 'resize-col'">
              {{displayHeadCharactor(col)}}
            </div>
          </template>
          <template v-if="type === 'resize-col'">
            {{displayHeadCharactor(col)}}
          </template>
        </td>
      </tr>
      <tr v-for="(cellRow, row) in cells" v-if="row !== 0" v-bind:key="row" draggable="false" ondragstart="javascript: return false;">
        <!---->
        <td class="fe-content-table-table-th-1 noselect" draggable="false" ondragstart="javascript: return false;" row-header
            :row="row"
            :style="{
              height: (getRowHeight(row) || defaultRowHeight) + 'px',
              background: row >= area.beginCell.row && row <= area.endCell.row ? '#abd5ff' : ''
            }"
        >
          <!--<template v-if="type !== 'resize-row'">-->
            <!--<div row-header-div :row="row" v-if="type !== 'resize-row'" :style1="{height: ((getRowHeight(row) || defaultRowHeight) - 4) + 'px', lineHeight: ((getRowHeight(row) || defaultRowHeight) - 4) + 'px'}">-->
              <!--<span row-header-div :row="row">{{row}}</span>-->
            <!--</div>-->
          <!--</template>-->
          <!--<template v-if="type === 'resize-row'">-->
            <!--{{row}}-->
          <!--</template>-->
          <div row-header-div :row="row" :style="{cursor: type === 'resize-row' ? 'row-resize' : 'pointer' }">
            {{row}}
          </div>
        </td>
        <td v-for="(cell, col) in cellRow" v-if="col !== 0" :id="'td_' + row + '_' + col" v-bind:key="col + '@' + row" class="fe-content-table-table-td" v-show="isTdShow(row, col)" draggable="false" table-content
            :row="row"
            :col="col"
            :style="getCellStyle(row, col, cell)"
            :rowspan="getCellRowspan(row, col, cell)"
            :colspan="getCellColspan(row, col, cell)"
            v-bind:class="{
              'fe-content-table-copy': copy.row === row && copy.col === col,
              'style-bold-1': cell && cell.bold,
              'style-text-decoration-1': cell && cell.textDecoration,
              'style-italic-1': cell && cell.italic
            }"
        >
          <!--<div class="nodisplay" :id="'editor_' + row + '_' + col" v-if="editInCell && cacheEditCell.row === row && cacheEditCell.col === col"></div>-->
          <!--<div :id="'td_content_' + row + '_' + col" class="fe-content-table-table-td-div" draggable="false"-->
               <!--:style="{-->
                  <!--userSelect: isCellContentEditable(row, col) ? '' : 'none',-->
               <!--}"-->
               <!--v-html="getCellContent(row, col)"-->
               <!--v-if="!(editInCell && cacheEditCell.row === row && cacheEditCell.col === col)"-->
          <!--&gt;-->
          <!--</div>-->
          <el-input v-if="cell && cell.type === 'input'" size="mini" :readonly="true" :placeholder="cell.attribute.name" clearable :style="{ width: cell.attribute.width ? cell.attribute.width + 'px' : '100%' }"/>

          <el-input v-else-if="cell && cell.type === 'textarea'" size="mini" :readonly="true" :autosize="{ minRows: 2, maxRows: 4}" type="textarea" :row="10" resize="none" :placeholder="cell.attribute.name" clearable :style="{ width: cell.attribute.width ? cell.attribute.width + 'px' : '100%', height: cell.attribute.height ? cell.attribute.height + 'px' : '100%' }"/>

          <el-date-picker v-else-if="cell && cell.type === 'date'" size="mini" :readonly="true" type="date" :placeholder="cell.attribute.name" clearable :style="{ width: cell.attribute.width ? cell.attribute.width + 'px' : '100%' }"/>

          <el-select v-else-if="cell && cell.type === 'select'" v-model="cell.attribute.options[0].value" size="mini" :readonly="true" disabled :placeholder="cell.attribute.name" :style="{ width: cell.attribute.width ? cell.attribute.width + 'px' : '100%' }">
            <el-option
              v-for="item in cell.attribute.options"
              :key="item.value"
              :label="item.text"
              :value="item.value">
            </el-option>
          </el-select>

          <el-checkbox-group v-else-if="cell && cell.type === 'checkbox'" disabled size="mini">
            <template v-for="(item, index) in cell.attribute.options">
              <el-checkbox :label="item.text" :key="item.value">{{item.text}}</el-checkbox>
              <template v-if="!cell.attribute.horizon && index !== cell.attribute.options.length - 1"><br :key="'br' + item.value"/></template>
            </template>
          </el-checkbox-group>

          <el-radio-group v-else-if="cell && cell.type === 'radio'" disabled size="mini">
            <template v-for="(item, index) in cell.attribute.options">
              <el-radio :label="item.text" :key="item.value">{{item.text}}</el-radio>
              <template v-if="!cell.attribute.horizon && index !== cell.attribute.options.length - 1"><br :key="'br' + item.value"/></template>
            </template>
          </el-radio-group>

          <el-input v-else-if="cell && cell.type === 'numeric'" size="mini" :readonly="true" type="numeric" :placeholder="cell.attribute.name" clearable :style="{ width: cell.attribute.width ? cell.attribute.width + 'px' : '100%'}"/>

          <div v-else-if="cell && cell.type === 'sequence'">
            No.
          </div>

          <el-input v-else-if="cell && cell.type === 'refer'" size="mini" :readonly="true" type="refer" :placeholder="cell.attribute.name" clearable :style="{ width: cell.attribute.width ? cell.attribute.width + 'px' : '100%'}"/>

          <el-input v-else-if="cell && cell.type === 'macro'" size="mini" :readonly="true" type="macro" :placeholder="cell.attribute.name" clearable :style="{ width: cell.attribute.width ? cell.attribute.width + 'px' : '100%'}"/>

          <el-input v-else-if="cell && cell.type === 'selectCode'" size="mini" :readonly="true" type="selectCode" :placeholder="cell.attribute.name" clearable :style="{ width: cell.attribute.width ? cell.attribute.width + 'px' : '100%'}"/>

          <el-input v-else-if="cell && cell.type === 'listenerBill'" size="mini" :readonly="true" type="listenerBill" :placeholder="cell.attribute.name" clearable :style="{ width: cell.attribute.width ? cell.attribute.width + 'px' : '100%'}"/>

          <div v-else :contenteditable="isCellContentEditable(row, col)" :id="'td_content_' + row + '_' + col" class="fe-content-table-table-td-div" draggable="false"
               :style="{
                  userSelect: isCellContentEditable(row, col) ? '' : 'none',
               }"
               v-html="getCellContent(row, col, cell)"
               @blur="handleContentEditableBlur({row, col})"
          >
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import { isEventParentHasAttribute, toNumber, num2Char } from '@/util'
export default {
  name: 'ContentTable',
  computed: {
    area () {
      return this.$store.state.area
    },
    detail () {
      return this.$store.state.detail
    },
    type () {
      return this.$store.state.cache.type
    },
    selectedColIndexList () {
      return this.$store.state.selectedColIndexList
    },
    rows () {
      return this.$store.state.rows
    },
    rowNum () {
      return this.$store.state.rowNum
    },
    cols () {
      return this.$store.state.cols
    },
    colNum () {
      return this.$store.state.colNum
    },
    defaultColWidth () {
      return this.$store.state.config.defaultColWidth
    },
    defaultRowHeight () {
      return this.$store.state.config.defaultRowHeight
    },
    begin () {
      return this.$store.state.cache.select.selectedArea.begin
    },
    end () {
      return this.$store.state.cache.select.selectedArea.end
    },
    cells () {
      return this.$store.state.cells
    },
    beginCell () {
      return this.$store.state.cache.select.beginCell
    },
    cacheEditCell () {
      return this.$store.state.cache.editCell
    },
    borderDefault () {
      return this.$store.state.config.borderDefault
    },
    borderDefaultColor () {
      return this.$store.state.config.borderDefaultColor
    },
    borderDefaultStyle () {
      return this.$store.state.config.borderDefaultStyle
    },
    borderDefaultWidth () {
      return this.$store.state.config.borderDefaultWidth
    },
    selectedBorderDefaultColor () {
      return this.$store.state.config.selectedBorderDefaultColor
    },
    selectedBorderDefaultStyle () {
      return this.$store.state.config.selectedBorderDefaultStyle
    },
    selectedBorderDefaultWidth () {
      return this.$store.state.config.selectedBorderDefaultWidth
    },
    selectedBorderDefault () {
      return this.$store.state.config.selectedBorderDefault
    },
    selectedCellBackground () {
      return this.$store.state.config.selectedCellBackground
    },
    copy () {
      return this.$store.state.cache.copy
    },
    editInCell () {
      return this.$store.state.config.editInCell
    },
    timestamp () {
      return this.$store.state.timestamp
    },
    totalWidth () {
      let defaultColWidth = this.$store.state.config.defaultColWidth
      let totalWidth = this.$store.state.cols.reduce((total, col) => {
        if (col) {
          return total + (col.width || defaultColWidth - 0) - 0
        }
        return total + defaultColWidth - 0
      }, 0)
      return totalWidth
    },
    copyCell () {
      return this.$store.state.cache.copyCell
    }
  },
  data: function () {
    return {
      windowWidth: null,
      windowHeight: null,
      // tableOffsetTop: 0,
      // tableOffsetLeft: 0,
      mouseoverRow: undefined,
      mouseoverCol: undefined,
      thisTimeStamp: 0,
      focused: false
    }
  },
  mounted () {
    // this.tableOffsetTop = document.querySelector('#fe-content-table-table').offsetTop
    // this.tableOffsetLeft = document.querySelector('#fe-content-table-table').offsetLeft
    // this.tableOffsetTop = document.querySelector('#fe-content-table-table').screenY
    // this.tableOffsetLeft = document.querySelector('#fe-content-table-table').screenX

    this.windowWidth = window.innerWidth
    this.windowHeight = window.innerHeight

    // 
    if (!this.$store.state.cache.select.selectedArea.begin.row) {
      this.beginSelectCell({row: 1, col: 1})
      this.endSelectCell({row: 1, col: 1})
    }

    // this.bodyListener = (e) => {
    //   if (e.which === 67 && e.ctrlKey) {
    //     console.log('ctrl + c')
    //     if (!this.type) {
    //       console.log(e)
    //       this.setCopyCellArea()
    //     }
    //   }
    // }
    // this.$el.addEventListener('keydown', (e) => {
    //   console.log(1111)
    //   if (e.which === 67 && e.ctrlKey) {
    //     console.log('ctrl + c')
    //     if (!this.type) {
    //       console.log(e)
    //       this.setCopyCellArea()
    //     }
    //   }
    // }, false)
  },
  methods: {
    handleFocus (event) {
      this.focused = true
    },
    handleBlur (event) {
      this.focused = false
    },
    isTdShow (row, col) {
      return !this.cells[row][col] || !this.cells[row][col].hidden
    },
    showMenu (e) {
      let isCopying = this.copy.row && this.copy.col
      if (e.target.hasAttribute('col-header') || e.target.hasAttribute('col-header-div')) {
        let menus = [
          {
            icon: 'fe-iconfont-insert-column',
            text: '()',
            disabled: false,
            handler: () => {
              this.insertCol()
              this.recordState()
            }
          },
          {
            icon: 'fe-iconfont-delete-column',
            text: '',
            disabled: false,
            handler: () => {
              this.deleteCol()
              this.recordState()
            }
          }
        ]
        if (isCopying) {
          menus.splice(0, 1)
        }

        // 
        this.showContentMenu({
          e: e,
          width: 135,
          menus: menus
        })
      } else if (e.target.hasAttribute('row-header') || e.target.hasAttribute('row-header-div')) {
        let menus = [
          {
            icon: 'fe-iconfont-insert-row',
            text: '()',
            disabled: false,
            handler: () => {
              this.insertRow()
              this.recordState()
            }
          },
          {
            icon: 'fe-iconfont-delete-row',
            text: '',
            disabled: false,
            handler: () => {
              this.deleteRow()
              this.recordState()
            }
          }
        ]
        if (isCopying) {
          menus.splice(0, 1)
        }

        // 
        this.showContentMenu({
          e: e,
          width: 135,
          menus: menus
        })
      }
    },
    displayHeadCharactor (i) {
      // let charactors = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
      // // // debugger
      // // if (i === 26) {
      // //   // debugger
      // // }
      // let time = parseInt((i - 1) / 26)
      // let aux = (i - 1) % 26
      // let first = charactors.substring(time - 1, time)
      // let last = charactors.substring(aux, aux + 1)
      // return first + last
      return num2Char(i)
    },
    // getCellBorderTop (row, col) {
    //   if (row === this.begin.row && col >= this.begin.col && col <= this.end.col) {
    //     return this.selectedBorderDefault
    //   }
    //
    //   if (this.cells[row][col] && this.cells[row][col].borderTopWidth) {
    //     return this.cells[row][col].borderTopStyle + ' ' + this.cells[row][col].borderTopColor + ' ' + this.cells[row][col].borderTopWidth + 'px'
    //   }
    //   return 'solid 1px ' + this.borderDefaultColor
    // },
    // getCellBorderLeft (row, col) {
    //   if (col === this.begin.col && row >= this.begin.row && row <= this.end.row) {
    //     return this.selectedBorderDefault
    //   }
    //
    //   if (this.cells[row][col] && this.cells[row][col].borderLeftWidth) {
    //     return this.cells[row][col].borderLeftStyle + ' ' + this.cells[row][col].borderLeftColor + ' ' + this.cells[row][col].borderLeftWidth + 'px'
    //   }
    //   return 'solid 1px ' + this.borderDefaultColor
    // },
    // getCellBorderBottom (row, col) {
    //   if ((row === this.end.row || (row + this.getCellRowspan(row, col) - 1) === this.end.row) && col >= this.begin.col && col <= this.end.col) {
    //     return this.selectedBorderDefault
    //   }
    //
    //   if (this.cells[row][col] && this.cells[row][col].borderBottomWidth) {
    //     return this.cells[row][col].borderBottomStyle + ' ' + this.cells[row][col].borderBottomColor + ' ' + this.cells[row][col].borderBottomWidth + 'px'
    //   }
    //   return 'solid 1px ' + this.borderDefaultColor
    // },
    // getCellBorderRight (row, col) {
    //   if ((col === this.end.col || (col + this.getCellColspan(row, col) - 1) === this.end.col) && row >= this.begin.row && row <= this.end.row) {
    //     return this.selectedBorderDefault
    //   }
    //
    //   if (this.cells[row][col] && this.cells[row][col].borderRightWidth) {
    //     return this.cells[row][col].borderRightStyle + ' ' + this.cells[row][col].borderRightColor + ' ' + this.cells[row][col].borderRightWidth + 'px'
    //   }
    //   return 'solid 1px ' + this.borderDefaultColor
    // },
    getCellBorderTop (row, col, cell) {
      if (row === this.copyCell.begin.row && col >= this.copyCell.begin.col && col <= this.copyCell.end.col) {
        return 'double 3px #409EFF'
      }

      // if (cell && cell.selected && cell.selected_is_top) {
      //   return this.selectedBorderDefault
      // }
      if (cell && cell.selected && (row - 0) === (this.begin.row - 0)) {
        return this.selectedBorderDefault
      }

      if (row === this.detail.beginCell.row && col >= this.detail.beginCell.col && col <= this.detail.endCell.col) {
        return 'dotted 2px #000000'
      }

      if (cell && cell.borderTop) {
        return cell.borderTop
      }
      return this.borderDefault
    },
    getCellBorderLeft (row, col, cell) {
      if (col === this.copyCell.begin.col && row >= this.copyCell.begin.row && row <= this.copyCell.end.row) {
        return 'double 3px #409EFF'
      }

      // if (cell && cell.selected && cell.selected_is_left) {
      //   return this.selectedBorderDefault
      // }
      if (cell && cell.selected && (col - 0) === (this.begin.col - 0)) {
        return this.selectedBorderDefault
      }

      if (row >= this.detail.beginCell.row && row <= this.detail.endCell.row && col === this.detail.beginCell.col) {
        return 'dotted 2px #000000'
      }

      if (cell && cell.borderLeft) {
        return cell.borderLeft
      }
      return this.borderDefault
    },
    getCellBorderBottom (row, col, cell) {
      if (row === this.copyCell.end.row && col >= this.copyCell.begin.col && col <= this.copyCell.end.col) {
        return 'double 3px #409EFF'
      }
      if (cell && cell.rowspan && (row - 0 + cell.rowspan - 1) === this.copyCell.end.row && row >= this.copyCell.begin.row && row <= this.copyCell.end.row) {
        return 'double 3px #409EFF'
      }

      // if (cell && cell.selected && cell.selected_is_bottom) {
      //   return this.selectedBorderDefault
      // }
      if (cell && cell.selected && ((row - 0) === (this.end.row - 0) || (row + cell.rowspan - 1) === (this.end.row - 0))) {
        return this.selectedBorderDefault
      }

      if (row === this.detail.endCell.row && col >= this.detail.beginCell.col && col <= this.detail.endCell.col) {
        return 'dotted 2px #000000'
      }

      if (cell && cell.borderBottom) {
        return cell.borderBottom
      }
      return this.borderDefault
    },
    getCellBorderRight (row, col, cell) {
      if (col === this.copyCell.end.col && row >= this.copyCell.begin.row && row <= this.copyCell.end.row) {
        return 'double 3px #409EFF'
      }
      if (cell && cell.colspan && (col - 0 + cell.colspan - 1) === this.copyCell.end.col && row >= this.copyCell.begin.row && row <= this.copyCell.end.row) {
        return 'double 3px #409EFF'
      }

      // if (cell && cell.selected && cell.selected_is_right) {
      //   return this.selectedBorderDefault
      // }
      if (cell && cell.selected && ((col - 0) === (this.end.col - 0) || (col + cell.colspan - 1) === (this.end.col - 0))) {
        return this.selectedBorderDefault
      }

      if (row >= this.detail.beginCell.row && row <= this.detail.endCell.row && col === this.detail.endCell.col) {
        return 'dotted 2px #000000'
      }

      if (cell && cell.borderRight) {
        return cell.borderRight
      }
      return this.borderDefault
    },
    getCellStyle (row, col, cell) {
      let style = {
        borderTop: this.getCellBorderTop(row, col, cell),
        borderLeft: this.getCellBorderLeft(row, col, cell),
        borderBottom: this.getCellBorderBottom(row, col, cell),
        borderRight: this.getCellBorderRight(row, col, cell),
        background: this.getCellBackground(row, col, cell),
        textAlign: this.getCellTextalign(row, col, cell),
        verticalAlign: this.getCellVertialalign(row, col, cell),
        fontSize: this.getCellTextFontSize(row, col, cell),
        fontFamily: this.getCellTextFontFamily(row, col, cell),
        color: this.getCellTextColor(row, col, cell)
      }

      return style
    },
    isColSelected (colIndex) {
      for (let i in this.selectedColIndexList) {
        if (this.selectedColIndexList[i] === colIndex) {
          return true
        }
      }
      return false
    },
    getCell (row, col) {
      if (this.cells[row][col]) {
        return this.cells[row][col]
      }
      return {}
    },
    getCellContent (row, col, cell) {
      if (cell) {
        return cell.content
      }
      return ''
    },
    isCellContentEditable (row, col) {
      return this.cacheEditCell.row === row && this.cacheEditCell.col === col
    },
    getCellRowspan (row, col, cell) {
      if (cell) {
        return cell.rowspan
      }
      return undefined
    },
    getCellColspan (row, col, cell) {
      if (cell) {
        return cell.colspan
      }
      return undefined
    },
    getCellTextalign (row, col) {
      if (this.cells[row][col]) {
        return this.cells[row][col].textAlign
      }
      return undefined
    },
    getCellVertialalign (row, col) {
      if (this.cells[row][col]) {
        return this.cells[row][col].verticalAlign
      }
      return undefined
    },
    getCellTextFontSize (row, col) {
      if (this.cells[row][col]) {
        return this.cells[row][col].fontSize || this.$store.state.config.defaultFontSize
      }
      return this.$store.state.config.defaultFontSize
    },
    getCellTextFontFamily (row, col) {
      if (this.cells[row][col]) {
        return this.cells[row][col].fontFamily || this.$store.state.config.defaultFontFamily
      }
      return this.$store.state.config.defaultFontFamily
    },
    getCellTextColor (row, col) {
      if (this.cells[row][col]) {
        return this.cells[row][col].color
      }
      return undefined
    },
    getCellBackground (row, col, cell) {
      if (cell && cell.background) {
        return cell.background
      }

      if (cell && cell.selected) {
        return this.selectedCellBackground
      }

      // if (row >= this.begin.row && row <= this.end.row && col >= this.begin.col && col <= this.end.col) {
      //   if (this.beginCell.row === row && this.beginCell.col === col) {
      //     if (this.cells[row][col]) {
      //       return this.cells[row][col].background || '#FFFFFF'
      //     } else {
      //       return '#FFFFFF'
      //     }
      //   }
      //   if (this.cells[row][col]) {
      //     return this.cells[row][col].background || '#eaecf5'
      //   } else {
      //     return '#eaecf5'
      //   }
      // }
      //
      // if (this.cells[row][col]) {
      //   return this.cells[row][col].background
      // }
      // return '#FFFFFF'
    },
    getColWidth (col) {
      return this.cols[col].width
    },
    getRowHeight (row) {
      return this.rows[row].height
    },
    handleMouseMove (e) {
      let isColHeader = e.target.hasAttribute('col-header')
      let isColHeaderDiv = e.target.hasAttribute('col-header-div')
      let isRowHeader = e.target.hasAttribute('row-header')
      let isRowHeaderDiv = e.target.hasAttribute('row-header-div')

      if (this.type === 'resize-col' && (isColHeader || isColHeaderDiv)) {
        this.dragResizeCol({location: {x: e.clientX, y: e.clientY}})
        e.target.style.cursor = 'col-resize'
      } else if (this.type === 'resize-row' && (isRowHeader || isRowHeaderDiv)) {
        this.dragResizeRow({location: {x: e.clientX, y: e.clientY}})
      }
    },
    handleMouseOver (e) {
      let target = null
      let isTableContent = (e.target.hasAttribute('table-content')) || (target = isEventParentHasAttribute(e, 'table-content'))
      if (isTableContent && !target) {
        target = e.target
      }

      if (isTableContent) {
        let row = toNumber(target.getAttribute('row') || 1)
        let col = toNumber(target.getAttribute('col') || 1)

        if (row === this.mouseoverRow && col === this.mouseoverCol) {
          return
        }

        if ((this.type === 'select' || this.type === 'edit-cell')) {
          this.dragSelect({event: e, row, col})
        }
      }
    },
    handleMouseDown (e) {
      // if (e.button !== 0) {
      //   return false
      // }
      let isColHeader = e.target.hasAttribute('col-header')
      let isColHeaderDiv = e.target.hasAttribute('col-header-div')
      let isRowHeader = e.target.hasAttribute('row-header')
      let isRowHeaderDiv = e.target.hasAttribute('row-header-div')
      let isTableSelector = e.target.hasAttribute('table-selector')
      let target = null
      let isTableContent = (e.target.hasAttribute('table-content')) || (target = isEventParentHasAttribute(e, 'table-content'))
      if (isTableContent && !target) {
        target = e.target
      }
      let targetCol = toNumber(e.target.getAttribute('col') || 1)
      let targetRow = toNumber(e.target.getAttribute('row') || 1)
      if (isColHeaderDiv) {
        this.selectCol(targetCol)
      } else if (isRowHeaderDiv) {
        this.selectRow(targetRow)
      } else if (isColHeader) {
        // 
        if (e.offsetX <= 2 || (e.target.clientWidth - e.offsetX) <= 2) {
          if (e.target.clientWidth >= 7) {
            if (e.offsetX <= 2) {
              targetCol = targetCol - 1
            }
          }
          this.beginResizeCol({col: targetCol, beginLocation: {x: e.clientX, y: e.clientY}})
        }
        // else {
        //   this.selectCol(targetCol)
        // }
      } else if (isRowHeader) {
        // 
        if (e.offsetY <= 2 || (e.target.clientHeight - e.offsetY) <= 2) {
          if (e.target.clientHeight >= 7) {
            if (e.offsetY <= 2) {
              targetRow = targetRow - 1
            }
          }
          this.beginResizeRow({row: targetRow, beginLocation: {x: e.clientX, y: e.clientY}})
        }
        // else {
        //   this.selectRow(targetRow)
        // }
      } else if (isTableContent) {
        let row = toNumber(target.getAttribute('row') || 1)
        let col = toNumber(target.getAttribute('col') || 1)
        this.beginSelectCell({event: e, row, col})
      } else if (isTableSelector) {
        // TODO
        // 
        // this.selectTable()
        // this.beginSelectCell({event: e, row: 1, col: 1})
        // this.dragSelect({event: e, row: this.$store.state.rowNum, col: this.$store.state.colNum})
        // this.endSelectCell({event: e, row: this.$store.state.rowNum, col: this.$store.state.colNum})
      }
    },
    handleMouseUp (e) {
      if (this.type === 'resize-col') {
        this.endResizeCol({location: {x: e.clientX, y: e.clientY}})
        this.recordState()
      } else if (this.type === 'resize-row') {
        this.endResizeRow({location: {x: e.clientX, y: e.clientY}})
        this.recordState()
      } else if (this.type === 'select') {
        let target = null
        let isTableContent = (e.target.hasAttribute('table-content')) || (target = isEventParentHasAttribute(e, 'table-content'))
        if (isTableContent && !target) {
          target = e.target
        }
        let row = toNumber(target.getAttribute('row') || 1)
        let col = toNumber(target.getAttribute('col') || 1)
        if (isTableContent) {
          this.endSelectCell({event: e, row, col})
        }
      }

      // BUG ctrl+c
      this.focused = true
    },
    handleMouseLeave (e) {
    },
    handleClick (e) {
      // this.endEditCell()
    },
    handleMouseDblClick (e) {
      let target = null
      let isTableContent = (e.target.hasAttribute('table-content')) || (target = isEventParentHasAttribute(e, 'table-content'))
      if (isTableContent && !target) {
        target = e.target
      }

      if (isTableContent) {
        let row = toNumber(target.getAttribute('row') || 1)
        let col = toNumber(target.getAttribute('col') || 1)
        this.beginEditCell({row, col})
      }
    },
    handleHeadMouseLeave (e) {
      if (this.type === 'resize-col') {
        this.endResizeCol({location: {x: e.clientX, y: e.clientY}})
        this.recordState()
      } else if (this.type === 'resize-row') {
        this.endResizeRow({location: {x: e.clientX, y: e.clientY}})
        this.recordState()
      }
    },
    getTableWidth () {
      // let width = 0
      // console.log('cols: ', this.cols)
      // for (let i in this.cols) {
      //   let col = this.cols[i]
      //   console.log(col)
      //   if (!col || !col.width) {
      //     console.log(i, '1: ', this.defaultColWidth)
      //     width = width + this.defaultColWidth
      //   } else {
      //     width = width + col.width
      //     console.log(i, '2: ', col.width)
      //   }
      // }
      // console.log(width)
      // return width + 15
      let width = 0
      for (let i in this.cells[1]) {
        let col = this.cols[i]
        if (!col || !col.width) {
          width = width + this.defaultColWidth
        } else {
          width = width + col.width
        }
      }
      return width + 15
    },
    handleContentEditableBlur ({row, col}) {
      this.endEditCell({row, col})
      this.recordState()
    },
    ...mapActions([
      'selectCol',
      'selectRow',
      'selectTable',
      'beginSelectCell',
      'endSelectCell',
      'dragSelect',
      'exitSelectMode',
      'beginEditCell',
      'endEditCell',
      'beginResizeCol',
      'endResizeCol',
      'dragResizeCol',
      'beginResizeRow',
      'endResizeRow',
      'dragResizeRow',
      'showContentMenu',
      'insertCol',
      'deleteCol',
      'insertRow',
      'deleteRow',
      'setCopyCellArea',
      'recordState'
    ])
  },
  beforeUpdate () {
  },
  updated () {
  }
}
</script>

<style scoped lang="scss">
  .noselect {
    user-select: none;
  }
  .fe-content-table-table-line0 {
    padding-left: 2px;
    padding-right: 2px;
    cursor: col-resize;
    &:first-child {
      padding-left: 10px !important;
      padding-right: 10px;
    }
    div {
      /*padding: 2px;*/
      cursor: pointer;
      height: 18px;
      line-height: 18px;
      /*vertical-align: middle;*/
      /*line-height: 24px;*/
    }
  }
  /*.fe-content-table-table-line0:first-child {*/
    /*padding-left: 10px !important;*/
    /*padding-right: 10px;*/
  /*}*/
  /*.fe-content-table-table-line0 > div {*/
    /*height: calc(100% + 2px);*/
    /*padding: 2px;*/
  /*}*/
  .fe-content-table-container {
    font-size: 14px;
    width: calc(100%);
    height: calc(100%);
    /*overflow-x: scroll;*/
    /*overflow-y: scroll;*/
  }
  .fe-content-table-table {
    border-collapse: collapse;
    border-spacing: 0px;
    table-layout: fixed;
    /*width: 100%;*/
    /*max-width: 100%;*/
  }
  .fe-content-table-table tr {
    /*border: solid 1px #ddd;*/
  }
  .fe-content-table-table tr:first-child > td:first-child  {
    background: #a9c4e9;
    border: solid 1px #ddd;
    text-align: center;
    font-size: 13px;
  }
  .fe-content-table-table tr:first-child > td {
    background: #e2e9f0;
    border: solid 1px #ddd;
    text-align: center;
    font-size: 13px;
  }
  .fe-content-table-table tr > td:first-child {
    background: #e2e9f0;
    font-size: 13px;
  }
  .fe-content-table-table-td {
    /*border: solid 1px #ddd;*/
    /*text-align: center;*/
    cursor: cell;
    height: 0px;
    word-wrap: break-word;
    word-break: break-all;
    font-size: 12px;
    font-family: SimSun ,serif;
    overflow: hidden;
    vertical-align: top;
    padding: 1px;
  }
  .fe-content-table-table-td textarea{
    width: 100%;
    height: 100%;
  }
  .fe-content-table-table-th-1 {
    background: #e2e9f0;
    border: solid 1px #ddd;
    font-size: 13px;
    text-align: center;
    cursor: row-resize;
    padding-top: 1px;
    padding-bottom: 1px;
    vertical-align: middle;
    div {
      /*cursor: pointer;*/
      height: calc(100% - 4px);
      /*line-height: calc(100% - 4px);*/
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .fe-content-table-table-td-div {
    width: 100%;
    overflow:hidden;
    /*height: 100%;*/
  }
  .fe-content-table-copy {
    -moz-box-shadow:0 0 10px #06c;
    -webkit-box-shadow:0 0 10px #06c;
    box-shadow:0 0 10px #06c;
  }

  .style-bold-0 {
    font-weight: normal;
  }
  .style-bold-1 {
    font-weight: bold;
  }

  .style-text-decoration-0 {
    text-decoration: none;
  }
  .style-text-decoration-1 {
    text-decoration: underline;
  }

  .style-italic-0 {
    font-style: normal;
  }
  .style-italic-1 {
    font-style: italic;
  }

</style>



