
// import { Base64 } from 'js-base64'

let token = window.name.split('|')[0]
let refreshToken = window.name.split('|')[1]
// let formKey = window.name.split('|')[2]
// let formId = window.name.split('|')[3]

let getUrlParam = function (name) {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
  let r = window.location.search.substr(1).match(reg)
  if (r != null) {
    return unescape(r[2])
  }
  return null
}

let formId = getUrlParam('formId')
let formKey = getUrlParam('formKey')
let tenantId = getUrlParam('t')

