<template>
  <div>
    <div class="hljs" v-html="compiledMarkdown()" v-highlight></div>
  </div>
</template>

<script>
import Marked from 'marked'

export default {
  name: 'Help',
  comments: {
    Marked
  },
  computed: {
  },
  methods: {
    compiledMarkdown () {
      let markDown = `
### 
| # | |  | |  |
| --------: | | :-- | :--------| :-- |
| 1 | | Ctrl + C | |    |
| 2 | | Ctrl + V | |   |
| 3 | | Ctrl + Z | |  |
| 4 | | Ctrl + Y | |  |
| 5 | | Ctrl + S | |  |
| 6 | | F1      | |  |
| 7 | | F2      | |  |
| 8 | | ESC      | |  |
| 9 | | DELETE      | |  |
      `

      return Marked(markDown, { sanitize: true })
    }
  }
}
</script>

<style scoped>

</style>



