import localforage from 'localforage'

localforage.config({
  driver: localforage.INDEXEDDB, // Force WebSQL; same as using setDriver()
  name: 'FormEditor',
  version: 1.0,
  storeName: 'FormEditorDB', // Should be alphanumeric, with underscores.
  description: 'FormEditorDB.'
})

let contains = document.documentElement.contains
  ? function (parent, node) {
    return parent !== node && parent.contains(node)
  }
  : function (parent, node) {
    while (node && (node = node.parentNode)) { if (node === parent) return true }
    return false
  }

let isEventParentHasAttribute = function (e, attributeName) {
  for (let i in e.path) {
    if (e.path[i] === document.querySelector('body')) {
      return false
    }
    if (e.path[i].hasAttribute(attributeName)) {
      return e.path[i]
    }
  }
  return false
}

let toNumber = function (val) {
  return val - 0
}

let getLocalStorageItem = function (key, callback) {
  // debugger
  localforage.getItem(key, (err, val) => {
    if (!err && val) {
      callback(val)
    }
  })
}

let setLocalStorageItem = function (key, value) {
  // debugger
  return localforage.setItem(key, value)
}

let isInArea = function (row, col, area) {
  if (!row || !col || !area) {
    return false
  }

  if (!area.beginCell || !area.endCell) {
    return false
  }

  if (!area.beginCell.row || !area.beginCell.col) {
    return false
  }

  if (!area.endCell.row || !area.endCell.col) {
    return false
  }

  return row >= area.beginCell.row && row <= area.endCell.row && col >= area.beginCell.col && col <= area.endCell.col
}

/**
 *  3 -> C;  27 -> AA
 * @param num
 * @returns {string}
 */
let num2Char = function (num) {
  let charactors = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
  let time = parseInt((num - 1) / 26)
  let aux = (num - 1) % 26
  let first = charactors.substring(time - 1, time)
  let last = charactors.substring(aux, aux + 1)
  return first + last
}

/**
 * 
 *
 * @param cells
 * @param name
 * @returns {string}
 */
let nextItemName = function (cells, name) {
  let itemNames = []
  for (let i in cells) {
    for (let j in cells) {
      let cell = cells[i][j]
      if (cell && cell.attribute && cell.attribute.name) {
        itemNames.push(cell.attribute.name)
      }
    }
  }

  let nextName = ''
  let nextId = 1
  do {
    nextName = `${name}${nextId}`
    if (itemNames.indexOf(nextName) >= 0) {
      nextId++
    } else {
      return nextName
    }

    if (nextId >= 9999) {
      return 'Name Generate Error'
    }
  } while (true)
}

/**
 * 
 * @param length
 * @returns {string}
 */
let getComplexRandomID = function (length) {
  length = length || 16
  var strings = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'
  var stringArr = strings.split('')

  // 
  stringArr.sort(function (a, b) {
    return Math.random() - Math.random()
  })

  var resultStrArr = []
  for (var i = 0; i < length; i++) {
    resultStrArr.push(stringArr[parseInt(Math.random() * 63)])
  }
  return resultStrArr.join('')
}

export {contains, isEventParentHasAttribute, toNumber, getLocalStorageItem, setLocalStorageItem, isInArea, num2Char, nextItemName, getComplexRandomID}



