<template>
  <div style="display: flex; flex-direction: column;">
    <toolbar ref="toolbar" @loading="emitLoading"></toolbar>
    <!--<hr style="margin: 2px; border: 0px;"/>-->
    <content-table ref="contentTable" @focus="handleFocus" @blur="handleBlur" v-bind:style="{width: windowWidth + 'px', height: windowHeight - 64 + 'px', overflowX: 'scroll', overflowY: 'scroll'}"></content-table>
  </div>
</template>

<script>

import AllSelector from './selector/AllSelector'
import ColSelector from './selector/ColSelector'
import RowSelector from './selector/RowSelector'
import Toolbar from './Toolbar'
import EditorToolbar from './EditorToolbar'
import ContentTable from './ContentTable'
import { mapActions } from 'vuex'

export default {
  name: 'FormEditor',
  components: {
    Toolbar,
    EditorToolbar,
    ContentTable,
    AllSelector,
    ColSelector,
    RowSelector
  },
  computed: {
    cacheType () {
      return this.$store.state.cache.type
    },
    toolbarMenuOpened () {
      return this.$refs.toolbar.$refs.fontFamily.visible ||
        this.$refs.toolbar.$refs.fontSize.visible ||
        this.$refs.toolbar.borderPropertyDialog
    },
    contentTableFocused () {
      return this.$refs.contentTable.focused
    },
    cache () {
      return this.$store.state.cache
    }
  },
  data: function () {
    return {
      bodyListener: null,
      windowWidth: null,
      windowHeight: null,
      focused: false
    }
  },
  methods: {
    handleFocus () {
      console.log('focus')
      this.focused = true
    },
    handleBlur () {
      console.log('blur')
      this.focused = false
    },
    emitLoading () {
      this.$emit('loading')
    },
    ...mapActions([
      'setCopyCellArea',
      'beginEditCell',
      'endEditCell',
      'clearCopyCellArea',
      'copyCell',
      'recordState'
    ])
  },
  mounted: function () {
    this.bodyListener = e => {
      if (e.which === 67 && e.ctrlKey) { // ctrl + c
        if (!this.contentTableFocused || this.$refs.toolbar.editControl.type) {
          return false
        }

        if (!this.type) {
          this.setCopyCellArea()
        }
      } else if (e.which === 86 && e.ctrlKey) { // ctrl + v
        if (!this.contentTableFocused || this.$refs.toolbar.editControl.type) {
          return false
        }

        if (!this.type) {
          this.copyCell({event: e})
          this.recordState()
        }
      } else if (e.which === 113) { // f2 key
        this.beginEditCell(this.cache.select.selectedArea.begin)
      } else if (e.which === 27) { // esc key
        this.endEditCell()
        this.clearCopyCellArea()
      }
    }

    document.body.addEventListener('keydown', this.bodyListener, false)
    this.windowWidth = window.innerWidth
    this.windowHeight = window.innerHeight
  },
  beforeDestroy () {
    document.body.removeEventListener('keydown', this.bodyListener)
  }
}
</script>

<style scoped>

</style>



