    <el-dialog :visible.sync="borderPropertyDialog" width="550px" custom-class="">
      <div slot="title" class="fe-toolbar-buttons-border-property-dialog-title">
        
      </div>
      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset" style="float: left; width: 200px;">
        <legend></legend>
        <el-select v-model="borderStyle" placeholder="" size="mini">
          <el-option
            v-for="item in borderPropertyStyleOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </fieldset>
      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset">
        <legend></legend>
        <el-button type="primary" size="mini" @click="setBorderNone">
          <i class="iconfont fe-iconfont-border-none">
            <span class="fe-toolbar-buttons-border-property-dialog-font-mini"></span>
          </i>
        </el-button>
        <el-button type="primary" size="mini" @click="setBorderOuter">
          <i class="iconfont fe-iconfont-border-outer">
            <span class="fe-toolbar-buttons-border-property-dialog-font-mini"></span>
          </i>
        </el-button>
        <el-button type="primary" size="mini" @click="setBorderInner">
          <i class="iconfont fe-iconfont-border-inner">
            <span class="fe-toolbar-buttons-border-property-dialog-font-mini"></span>
          </i>
        </el-button>
      </fieldset>

      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset" style="float: left; width: 200px;">
        <legend></legend>

        <swatches v-model="borderColor" colors="text-advanced" swatch-size="28">
        </swatches>

      </fieldset>

      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset">
        <legend></legend>
        <table class="fe-toolbar-buttons-border-property-dialog-table-border-buttons">
          <tr>
            <td>
              <el-button type="primary" size="mini" @click="setBorderTop">
                <i class="iconfont fe-iconfont-border-top"></i>
              </el-button>
            </td>
            <td colspan="3" rowspan="3">
              <table cellspacing="0" cellpadding="0" class="fe-toolbar-preview"
                     v-bind:class="{
                     }"
                     :style="{
                      borderTopColor: borderTop.color,
                      borderBottomColor: borderBottom.color,
                      borderLeftColor: borderLeft.color,
                      borderRightColor: borderRight.color,
                      borderTopStyle: borderTop.style,
                      borderBottomStyle: borderBottom.style,
                      borderLeftStyle: borderLeft.style,
                      borderRightStyle: borderRight.style,
                      borderTopWidth: borderTop.width,
                      borderBottomWidth: borderBottom.width,
                      borderLeftWidth: borderLeft.width,
                      borderRightWidth: borderRight.width,
                     }"
              >
                <tr>
                  <td :style="{
                      borderRightColor: borderVertical.color,
                      borderBottomColor: borderHorizon.color,
                      borderRightStyle: borderVertical.style,
                      borderBottomStyle: borderHorizon.style,
                      borderRightWidth: borderVertical.width,
                      borderBottomWidth: borderHorizon.width,
                     }"
                  ></td>
                  <td :style="{
                      borderBottomColor: borderHorizon.color,
                      borderBottomStyle: borderHorizon.style,
                      borderBottomWidth: borderHorizon.width
                     }"
                  ></td>
                </tr>
                <tr>
                  <td :style="{
                      borderRightColor: borderVertical.color,
                      borderRightStyle: borderVertical.style,
                      borderRightWidth: borderVertical.width
                     }"
                  ></td>
                  <td></td>
                </tr>
              </table>
            </td>
          </tr>
          <tr>
            <td>
              <el-button type="primary" size="mini" @click="setBorderHorizon">
                <i class="iconfont fe-iconfont-border-horizontal"></i>
              </el-button>
            </td>
          </tr>
          <tr>
            <td>
              <el-button type="primary" size="mini" @click="setBorderBottom">
                <i class="iconfont fe-iconfont-borderbottom"></i>
              </el-button>
            </td>
          </tr>
          <tr>
            <td></td>
            <td style="width: 50px; text-align: left">
              <el-button type="primary" size="mini" @click="setBorderLeft">
                <i class="iconfont fe-iconfont-borderleft"></i>
              </el-button>
            </td>
            <td style="width: 50px; text-align: center">
              <el-button type="primary" size="mini" @click="setBorderVertical">
                <i class="iconfont fe-iconfont-border-vertical"></i>
              </el-button>
            </td>
            <td style="width: 50px; text-align: right">
              <el-button type="primary" size="mini" @click="setBorderRight">
                <i class="iconfont fe-iconfont-border-right"></i>
              </el-button>
            </td>
          </tr>
        </table>
        <br/>
      </fieldset>
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="borderPropertyDialog = false"> </el-button>
        <el-button size="mini" type="primary" @click="handleSetCellsBorder({borderTop, borderHorizon, borderBottom, borderLeft, borderVertical, borderRight})"> </el-button>
      </div>
    </el-dialog>

    <!--input-->
    <el-dialog :visible.sync="editControl.type === 'input'" :width="controlDialog.dialogWidth" @open="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        
      </div>
      <el-form @submit.native.prevent :model="controlDialog.inputAttribute" ref="inputForm" :rules="controlDialog.inputRules" label-width="80px" size="mini">
        <el-form-item label="" prop="name">
          <el-input v-model.trim="controlDialog.inputAttribute.name" :autofocus="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="width">
          <el-input v-model.trim.number="controlDialog.inputAttribute.width" :min="50" :max="999" placeholder="50" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="maxlength">
          <el-input v-model.trim="controlDialog.inputAttribute.maxlength" :min="0" :max="999" :step="1" :controls="false" placeholder="200" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini"> </el-button>
        <el-button type="primary" @click="addInputSave" size="mini"> </el-button>
      </div>
    </el-dialog>

    <!--textarea-->
    <el-dialog :visible.sync="editControl.type === 'textarea'" :width="controlDialog.dialogWidth" @open="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        
      </div>
      <el-form @submit.native.prevent :model="controlDialog.textareaAttribute" ref="textareaForm" :rules="controlDialog.textareaRules" label-width="80px" size="mini">
        <el-form-item label="" prop="name">
          <el-input v-model.trim="controlDialog.textareaAttribute.name" :autofocus="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="width">
          <el-input v-model.trim.number="controlDialog.textareaAttribute.width" :min="50" :max="999" placeholder="50" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="maxlength">
          <el-input v-model.trim="controlDialog.textareaAttribute.maxlength" :min="0" :max="999" :step="1" :controls="false" placeholder="200" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini"> </el-button>
        <el-button type="primary" @click="addTextareaSave" size="mini"> </el-button>
      </div>
    </el-dialog>

    <!--date-->
    <el-dialog :visible.sync="editControl.type === 'date'" :width="controlDialog.dialogWidth" @open="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        
      </div>
      <el-form @submit.native.prevent :model="controlDialog.dateAttribute" ref="dateForm" :rules="controlDialog.dateRules" label-width="80px" size="mini">
        <el-form-item label="" prop="name">
          <el-input v-model.trim="controlDialog.dateAttribute.name" :autofocus="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="width">
          <el-input v-model.trim.number="controlDialog.dateAttribute.width" :min="0" :max="999" placeholder="60" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="height">

          <el-select v-model="controlDialog.dateAttribute.format" placeholder="" @change="controlDialog.dateAttribute.initVal = ''" style="width: 100% !important;">
            <el-option key="yyyy-MM-dd" label="yyyy-MM-dd" value="yyyy-MM-dd"/>
            <el-option key="yyyyMMdd" label="yyyyMMdd" value="yyyyMMdd"/>
            <el-option key="yyyy-MM-dd HH:mm" label="yyyy-MM-dd HH:mm" value="yyyy-MM-dd HH:mm"/>
            <el-option key="yyyyMMdd HH:mm" label="yyyyMMdd HH:mm" value="yyyyMMdd HH:mm"/>
          </el-select>

        </el-form-item>
        <el-form-item label="" prop="initVal">
          <el-select v-model="controlDialog.dateAttribute.initVal" placeholder="" style="width: 100% !important;" :clearable="true">
            <el-option key="yyyy-MM-dd" v-if="['yyyy-MM-dd', 'yyyyMMdd'].indexOf(controlDialog.dateAttribute.format) >= 0" label="" value="yyyy-MM-dd"/>
            <el-option key="yyyy-MM-01" v-if="['yyyy-MM-dd', 'yyyyMMdd'].indexOf(controlDialog.dateAttribute.format) >= 0" label="" value="yyyy-MM-01"/>
            <el-option key="yyyy-01-01" v-if="['yyyy-MM-dd', 'yyyyMMdd'].indexOf(controlDialog.dateAttribute.format) >= 0" label="" value="yyyy-01-01"/>
            <el-option key="yyyy-MM-dd HH:mm" v-if="['yyyy-MM-dd HH:mm', 'yyyyMMdd HH:mm'].indexOf(controlDialog.dateAttribute.format) >= 0" label="" value="yyyy-MM-dd HH:mm"/>
            <el-option key="yyyy-MM-01 00:00" v-if="['yyyy-MM-dd HH:mm', 'yyyyMMdd HH:mm'].indexOf(controlDialog.dateAttribute.format) >= 0" label="" value="yyyy-MM-01 00:00"/>
            <el-option key="yyyy-01-01 00:00" v-if="['yyyy-MM-dd HH:mm', 'yyyyMMdd HH:mm'].indexOf(controlDialog.dateAttribute.format) >= 0" label="" value="yyyy-01-01 00:00"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="readonly">
          <el-switch v-model="controlDialog.dateAttribute.readonly"></el-switch>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini"> </el-button>
        <el-button type="primary" @click="addDateSave" size="mini"> </el-button>
      </div>
    </el-dialog>

    <!--select-->
    <el-dialog :visible.sync="editControl.type === 'select'" :width="controlDialog.dialogWidth2" @open="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        
      </div>
      <el-form @submit.native.prevent :model="controlDialog.selectAttribute" ref="selectForm" :rules="controlDialog.selectRules" label-width="80px" size="mini">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <el-form-item label="" prop="name">
              <el-input v-model.trim="controlDialog.selectAttribute.name" :autofocus="true" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="width">
              <el-input v-model.trim.number="controlDialog.selectAttribute.width" :min="0" :max="999" placeholder="50" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
              :data="controlDialog.selectAttribute.options"
              height="280"
              border
              style="width: 100%">
              <el-table-column
                prop="value"
                label=""
                width="140">
                <template slot-scope="scope">
                  <el-input v-model.trim="scope.row.value" size="mini" placeholder=""></el-input>
                </template>
              </el-table-column>
              <el-table-column
                prop="label"
                label=""
                width="180">
                <template slot-scope="scope">
                  <el-input v-model.trim="scope.row.text" size="mini" placeholder=""></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="120">
                <template slot-scope="scope">
                  <el-button-group>
                    <el-button type="primary" size="mini" icon="el-icon-delete" @click="controlDialog.selectAttribute.options.splice(scope.$index, 1)"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-up" v-if="scope.$index !== 0" @click="controlDialog.selectAttribute.options.splice(scope.$index - 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-down" v-if="scope.$index !== controlDialog.selectAttribute.options.length - 1" @click="controlDialog.selectAttribute.options.splice(scope.$index + 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                  </el-button-group>
                  <!--<el-button type="text" size="mini" @click="controlDialog.selectAttribute.options.splice(scope.$index, 1)"></el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== 0" @click="controlDialog.selectAttribute.options.splice(scope.$index - 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])"></el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== controlDialog.selectAttribute.options.length - 1" @click="controlDialog.selectAttribute.options.splice(scope.$index + 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])"></el-button>-->
                </template>
