    items () {
      let items = []
      let cells = this.$store.state.cells
      let currentRow = this.$store.state.cache.select.beginCell.row
      let currentCol = this.$store.state.cache.select.beginCell.col
      for (let i in cells) {
        for (let j in cells[i]) {
          let cell = cells[i][j]
          if (cell && cell.attribute && cell.attribute.name && (currentRow !== (i - 0) || currentCol !== (j - 0))) {
            items.push({...cell.attribute, type: cell.type})
          }
        }
      }

      const types = ['input', 'textarea', 'numeric', 'refer']
      items = items.filter(item => types.indexOf(item.type) >= 0)

      // 
      items.sort((o1, o2) => {
        return (o1.name || '').localeCompare(o2.name || '')
      })

      return items
    },
