    undo () {
      this.clearCopyCellArea()
      getLocalStorageItem(CONST_LOCALSTORATE_STATELIST_KEY, val => {
        let stateList = JSON.parse(val || '[]')
        if (stateList <= 1) {
          return
        }

        this.$emit('loading')
        let stateIndex = this.$store.state.stateIndex
        stateIndex = stateIndex - 1
        if (stateIndex < 0) {
          this.$emit('loading')
          return
        }
        let backState = stateList[stateIndex]
        backState.stateIndex = stateIndex
        this.$store.replaceState(Object.assign({}, this.$store.state, backState))
        localStorage.setItem(CONST_LOCALSTORATE_STATEINDEX_KEY, stateIndex)
        this.$emit('loading')
        this.setRedoDisableState(false)
      })
    },
    redo () {
      this.clearCopyCellArea()
      getLocalStorageItem(CONST_LOCALSTORATE_STATELIST_KEY, val => {
        let stateList = JSON.parse(val || '[]')
        let stateIndex = this.$store.state.stateIndex
        if (stateIndex >= stateList.length - 1) {
          return
        }

        this.$emit('loading')
        stateIndex = stateIndex + 1
        let nextState = stateList[stateIndex]
        nextState.stateIndex = stateIndex
        this.$store.replaceState(Object.assign({}, this.$store.state, nextState))
        localStorage.setItem(CONST_LOCALSTORATE_STATEINDEX_KEY, stateIndex)

        getLocalStorageItem(CONST_LOCALSTORATE_STATELIST_KEY, val => {
          let stateList = JSON.parse(val || '[]')
          let stateIndex = localStorage.getItem(CONST_LOCALSTORATE_STATEINDEX_KEY)
          this.setRedoDisableState(stateIndex >= stateList.length - 1 || stateIndex === 0)
        })

        this.$emit('loading')
      })
    },
