    setDialogForm () {
      if (this.editControl.type === 'input') {
        this.controlDialog.inputAttribute.name = this.editControl.attribute.name
        this.controlDialog.inputAttribute.width = this.editControl.attribute.width
        this.controlDialog.inputAttribute.maxlength = this.editControl.attribute.maxlength
      } else if (this.editControl.type === 'textarea') {
        this.controlDialog.textareaAttribute.name = this.editControl.attribute.name
        this.controlDialog.textareaAttribute.width = this.editControl.attribute.width
        this.controlDialog.textareaAttribute.height = this.editControl.attribute.height
        this.controlDialog.textareaAttribute.maxlength = this.editControl.attribute.maxlength
      } else if (this.editControl.type === 'date') {
        this.controlDialog.dateAttribute.name = this.editControl.attribute.name
        this.controlDialog.dateAttribute.width = this.editControl.attribute.width
        this.controlDialog.dateAttribute.format = this.editControl.attribute.format
        this.controlDialog.dateAttribute.initVal = this.editControl.attribute.initVal
        this.controlDialog.dateAttribute.readonly = this.editControl.attribute.readonly
      } else if (this.editControl.type === 'select') {
        this.controlDialog.selectAttribute.name = this.editControl.attribute.name
        this.controlDialog.selectAttribute.width = this.editControl.attribute.width
        // this.controlDialog.selectAttribute.options = this.editControl.attribute.options
        this.controlDialog.selectAttribute.options = JSON.parse(JSON.stringify(this.editControl.attribute.options))
      } else if (this.editControl.type === 'checkbox') {
        this.controlDialog.checkboxAttribute.name = this.editControl.attribute.name
        this.controlDialog.checkboxAttribute.width = this.editControl.attribute.width
        this.controlDialog.checkboxAttribute.horizon = this.editControl.attribute.horizon
        this.controlDialog.checkboxAttribute.options = JSON.parse(JSON.stringify(this.editControl.attribute.options))
      } else if (this.editControl.type === 'radio') {
        this.controlDialog.radioAttribute.name = this.editControl.attribute.name
        this.controlDialog.radioAttribute.width = this.editControl.attribute.width
        this.controlDialog.radioAttribute.horizon = this.editControl.attribute.horizon
        this.controlDialog.radioAttribute.options = JSON.parse(JSON.stringify(this.editControl.attribute.options))
      } else if (this.editControl.type === 'numeric') {
        this.controlDialog.numericAttribute.name = this.editControl.attribute.name
        this.controlDialog.numericAttribute.width = this.editControl.attribute.width
        this.controlDialog.numericAttribute.precision = this.editControl.attribute.precision
        this.controlDialog.numericAttribute.min = this.editControl.attribute.min
        this.controlDialog.numericAttribute.max = this.editControl.attribute.max
        this.controlDialog.numericAttribute.numericItem = this.editControl.attribute.numericItem
        // this.controlDialog.numericAttribute.expressionItems = this.editControl.attribute.expressionItems || []
        // this.controlDialog.numericAttribute.relateItems = this.editControl.attribute.relateItems || []
        // this.controlDialog.numericAttribute.displayItems = this.editControl.attribute.displayItems || []
        this.controlDialog.numericAttribute.expressionItems = JSON.parse(JSON.stringify(this.editControl.attribute.expressionItems || []))
        this.controlDialog.numericAttribute.relateItems = JSON.parse(JSON.stringify(this.editControl.attribute.relateItems || []))
        this.controlDialog.numericAttribute.displayItems = JSON.parse(JSON.stringify(this.editControl.attribute.displayItems || []))

        this.controlDialog.numericItems.splice(0, 9999)
        this.controlDialog.numericAttribute.numericItem = ''
        for (let i in this.cells) {
          for (let j in this.cells[i]) {
            let cell = this.cells[i][j]
            if (cell && cell.type === 'numeric' && cell.attribute.name !== this.editControl.attribute.name) {
              // console.log('push cell attribute: ', cell.attribute)
              this.controlDialog.numericItems.push(cell.attribute)
            }
          }
        }
      } else if (this.editControl.type === 'sequence') {
        this.controlDialog.sequenceAttribute.name = this.editControl.attribute.name
      } else if (this.editControl.type === 'refer') {
        this.controlDialog.referAttribute.name = this.editControl.attribute.name
        this.controlDialog.referAttribute.width = this.editControl.attribute.width
        this.controlDialog.referAttribute.referCode = this.editControl.attribute.referCode
        this.controlDialog.referAttribute.referName = this.editControl.attribute.referName
        this.controlDialog.referAttribute.filterProps = this.editControl.attribute.filterProps
        this.controlDialog.referAttribute.multiple = this.editControl.attribute.multiple
        this.controlDialog.referAttribute.maps = JSON.parse(JSON.stringify(this.editControl.attribute.maps))
        this.resetReferInfo()

        this.resolveRefer(this.editControl.attribute.referCode, this.editControl.attribute.maps)
      } else if (this.editControl.type === 'macro') {
        this.controlDialog.macroAttribute.name = this.editControl.attribute.name
        this.controlDialog.macroAttribute.width = this.editControl.attribute.width
        this.controlDialog.macroAttribute.type = this.editControl.attribute.type
      } else if (this.editControl.type === 'selectCode') {
        this.controlDialog.selectCodeAttribute.name = this.editControl.attribute.name
        this.controlDialog.selectCodeAttribute.width = this.editControl.attribute.width
        this.controlDialog.selectCodeAttribute.selectType = this.editControl.attribute.selectType
        this.controlDialog.selectCodeAttribute.selectTypeName = this.editControl.attribute.selectTypeName
        this.resetSelectCodeInfo()

        this.resolveSelectCode(this.editControl.attribute.referCode)
      } else if (this.editControl.type === 'listenerBill') {
        this.controlDialog.listenerBillAttribute.name = this.editControl.attribute.name
        this.controlDialog.listenerBillAttribute.width = this.editControl.attribute.width
        this.controlDialog.listenerBillAttribute.listeners = this.editControl.attribute.listeners
      }
    },
    showSaveDialog () {
      {
        let items = []
        let errors = []
        for (let i in this.$store.state.cells) {
          for (let j in this.$store.state.cells[i]) {
            let cell = this.$store.state.cells[i][j]
            if (!cell) {
              continue
            }
            if (!cell.hidden && cell.type) {
              const item = {
                itemId: cell.attribute.itemid,
                itemName: cell.attribute.name,
                row: i,
                col: j
              }

              const conflictItem = items.find(tempItem => tempItem.itemId === item.itemId)
              if (conflictItem) {
                errors.push({row: i, col: j})
                const html = '<strong>[' + num2Char(j) + i + ']</strong><strong>[' + num2Char(conflictItem.col) + conflictItem.row + ']</strong>'
                Message.error({
                  dangerouslyUseHTMLString: true,
                  message: html,
                  showClose: true
                })
                return false
              }

              items.push(item)
            }
          }
        }
      }

      if (!this.area.beginCell.row || !this.area.beginCell.col || !this.area.endCell.row || !this.area.endCell.col) {
        Message.warning('')
        return false
      }

      const fit = this.isDetailFitArea()
      if (!fit) {
        Message.warning('')
        return false
      }
      // const inArea = this.isDetailInArea()
      // if (!inArea) {
      //   Message.warning('')
      //   return false
      // }

      this.saveDialog.visible = true
      this.saveDialog.saveForm.formKey = this.$store.state.form.formKey
      this.saveDialog.saveForm.name = this.$store.state.form.name
      this.saveDialog.saveForm.remark = this.$store.state.form.remark
      this.saveDialog.saveForm.formType = this.$store.state.form.formType
      // this.save(this.saveDialog.saveForm)
    },
    resetReferInfo () {
      this.controlDialog.referColumnList = []
      this.controlDialog.referInfo.code = ''
      this.controlDialog.referInfo.name = ''
    },
    resolveRefer (referCode, maps) {
      this.resetReferInfo()

      if (referCode) {
        let loading = this.$loading({
          fullscreen: true,
          text: '...'
        })
        let _this_ = this

        http.get('api/field/searchReferWindow?referCode=' + referCode)
          .then(function (response) {
            loading.close()
            // response = data

            if (response.data === null) {
              _this_.resetReferInfo()
              _this_.controlDialog.referAttribute.referCode = ''
              _this_.$message.error('code')
              return
            }

            // TODO
            _this_.controlDialog.referInfo.code = response.data.code
            _this_.controlDialog.referInfo.name = response.data.name
            _this_.controlDialog.referColumnList = response.data.columnList
            _this_.controlDialog.referAttribute.referCode = response.data.code
            _this_.controlDialog.referAttribute.referName = response.data.name
            _this_.controlDialog.referAttribute.referColumnList = response.data.columnList
            // _this_.controlDialog.referAttribute.referCode = response.data.code
            if (!maps) {
              _this_.controlDialog.referAttribute.maps = []
              response.data.columnList.forEach(() => _this_.controlDialog.referAttribute.maps.push({from: '', to: ''}))
            } else if (_this_.controlDialog.referAttribute.maps.length !== response.data.columnList.length) {
              for (let i = _this_.controlDialog.referAttribute.maps.length, end = response.data.columnList.length; i < end; i++) {
                _this_.controlDialog.referAttribute.maps.push({from: '', to: ''})
              }
            }
          })
          .catch(function (error) {
            console.log(error)
            loading.close()
            // _this_.controlDialog.referAttribute.referCode = ''
          })
      }
    },
    resetSelectCodeInfo () {
      this.controlDialog.selectCodeInfo.code = ''
      this.controlDialog.selectCodeInfo.name = ''
    },
    resolveSelectCode (selectType) {
      this.resetSelectCodeInfo()

      if (selectType) {
        let loading = this.$loading({
          fullscreen: true,
          text: '...'
        })
        let _this_ = this

        http.get('api/field/searchSelectCode?code=' + selectType)
          .then(function (response) {
            loading.close()
            // response = data

            if (response.data === null) {
              _this_.resetSelectCodeInfo()
              _this_.controlDialog.selectAttribute.selectType = ''
              _this_.$message.error('code')
              return
            }

            // TODO
            _this_.controlDialog.selectCodeInfo.code = response.data.code
            _this_.controlDialog.selectCodeInfo.name = response.data.codeName
            _this_.controlDialog.selectCodeAttribute.selectType = response.data.code
            _this_.controlDialog.selectCodeAttribute.selectTypeName = response.data.codeName
          })
          .catch(function (error) {
            console.log(error)
            loading.close()
          })
      }
    },
