    exportForm () {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then((param) => {
        let urlObject = window.URL || window.webkitURL || window
        let text = Base64.encode(JSON.stringify(this.$store.state, (k, v) => {
          if (v === undefined) {
            return 'undefined'
          }
          return v
        }))
        let exportBlob = new Blob([text])

        let saveLink = document.createElementNS('http://www.w3.org/1999/xhtml', 'a')
        saveLink.href = urlObject.createObjectURL(exportBlob)
        saveLink.download = param.value + '.fields'

        let ev = document.createEvent('MouseEvents')
        ev.initMouseEvent(
          'click', true, false, window, 0, 0, 0, 0, 0
          , false, false, false, false, 0, null
        )
        saveLink.dispatchEvent(ev)
      }).catch((e) => {
        console.log(e)
        // this.$message({
        //   type: 'info',
        //   message: ''
        // })
      })
    },
    importForm () {

    },
    readfile (file, fileList) {
      let _this_ = this
      var reader = new FileReader()
      reader.readAsText(file.raw)
      reader.onload = function () {
        try {
          let text = Base64.decode(reader.result)
          let state = JSON.parse(text, (k, v) => {
            if (v === 'undefined') {
              return undefined
            }
            return v
          })
          delete state.form
          delete state.formTypeList
          delete state.stateIndex
          delete state.codeList
          delete state.referWindowList
          delete state.editorToolbarStatus
          _this_.$store.replaceState(Object.assign({}, _this_.$store.state, state, {stateIndex: _this_.$store.state.stateIndex}))
          _this_.recordState()
        } catch (e) {
          console.log('Error', e)
          _this_.$message.error('')
        }
      }
      reader.onerror = function (e) {
        console.log('Error', e)
        _this_.$message.error('')
      }
    },
    isDetailFitArea () {
      if (this.$store.state.detail.beginCell.row) {
        // const areaEndCol = this.$store.state.cells
        let detailEndCol = -1
        for (let col = this.$store.state.detail.beginCell.col; col <= this.$store.state.detail.endCell.col; col++) {
          if (col > detailEndCol) {
            detailEndCol = col
          }
          let cell = this.$store.state.cells[this.$store.state.detail.beginCell.row][col]
          // if (cell && cell.col_end && cell.col_end > detailEndCol) {
          if (cell && cell.colspan && (col + cell.colspan - 1) > detailEndCol) {
            detailEndCol = col + cell.colspan - 1
          }
        }
        let areaEndCol = -1
        for (let col = this.$store.state.area.beginCell.col; col <= this.$store.state.area.endCell.col; col++) {
          if (col > areaEndCol) {
            areaEndCol = col
          }
          let cell = this.$store.state.cells[this.$store.state.area.beginCell.row][col]
          // if (cell && cell.col_end && cell.col_end > detailEndCol) {
          if (cell && cell.colspan && (col + cell.colspan - 1) > detailEndCol) {
            areaEndCol = col + cell.colspan - 1
          }
        }

        // if (this.$store.state.detail.beginCell.col !== this.$store.state.area.beginCell.col || areaEndCol !== detailEndCol) {
        //   return false
        // }
        return this.$store.state.detail.beginCell.col <= this.$store.state.area.beginCell.col && detailEndCol >= areaEndCol
      }
      return true
    },
    isDetailInArea () {
      if (this.$store.state.detail.beginCell.row) {
        if (this.$store.state.detail.beginCell.row >= this.$store.state.area.beginCell.row && this.$store.state.detail.endCell.row <= this.$store.state.area.endCell.row) {
          return true
        }
        return false
      }
      return true
    },
    handleDropdownClick (command) {
      if (command === 'macro') {
        this.addControlConfirm('macro')
      } else if (command === 'selectCode') {
        this.addControlConfirm('selectCode')
      } else if (command === 'listenerBill') {
        this.addControlConfirm('listenerBill')
      }
    },
    handleToolbarClick (e) {

    },
    queryReferWindow (queryString, cb) {
      const referWindowList = this.referWindowList
      const list = queryString ? referWindowList.filter(referWindow => (referWindow.code + referWindow.name).toLowerCase().indexOf(queryString.toLowerCase()) >= 0) : referWindowList
      //  callback 
      cb(list)
    },
    queryCode (queryString, cb) {
      const codeList = this.codeList
      const list = queryString ? codeList.filter(code => (code.code + code.codeName).toLowerCase().indexOf(queryString.toLowerCase()) >= 0) : codeList
      //  callback 
      cb(list)
    },
    handleSetArea () {
      this.setArea()
      this.recordState()
    },
    handleSetDetail () {
      this.setDetail()
      this.recordState()
    },
    handleClearDetail () {
      this.clearDetail()
      this.recordState()
    },
    handleSetCellsBackground (background) {
      this.setCellsBackground(background)
      this.recordState()
    },
    handleSetCellsTextColor (color) {
      this.setCellsTextColor(color)
      this.recordState()
    },
    handleSetTextFontSize (fontSize) {
      this.setCellsTextFontSize(fontSize)
      this.recordState()
    },
    handleSetTextFontFamily (fontFamily) {
      this.setCellsTextFontFamily(fontFamily)
      this.recordState()
    },
    handleSetBold () {
      this.setBold()
      this.recordState()
    },
    handleSetItalic () {
      this.setItalic()
      this.recordState()
    },
    handleSetTextDecoration () {
      this.setTextDecoration()
      this.recordState()
    },
    handleMergeCells () {
      this.mergeCells()
      this.recordState()
    },
    handleSplitCells () {
      this.splitCells()
      this.recordState()
    },
    handleSetCellsTextAlign (textAlign) {
      this.setCellsTextAlign(textAlign)
      this.recordState()
    },
    handleSetCellsTextValign (textAlign) {
      this.setCellsTextValign(textAlign)
      this.recordState()
    },
    handleSetCellsBorder ({borderTop, borderHorizon, borderBottom, borderLeft, borderVertical, borderRight}) {
      this.setCellsBorder({borderTop, borderHorizon, borderBottom, borderLeft, borderVertical, borderRight})
      this.recordState()
      this.borderPropertyDialog = false
    },
    ...mapActions([
      'beginSelectCell',
      'endSelectCell',
      'mergeSelectedCells',
      'mergeCells',
      'splitCells',
      'setCellsTextAlign',
      'setCellsTextValign',
      'setCellsBackground',
      'setCellsTextColor',
      'setCellsBorder',
      'beginUEdit',
      'doClearCellsContent',
      'doUndo',
      'doRedo',
      'copyCell',
      'setBold',
      'setTextDecoration',
      'setItalic',
      'setCellsTextFontSize',
      'setCellsTextFontFamily',
      'addControl',
      'beginAddControl',
      'cancelAddControl',
      'save',
      'setArea',
      'setDetail',
      'clearDetail',
      'setRedoDisableState',
      'clearCopyCellArea',
      'recordState'
    ])
  },
  mounted: function () {
    // console.log('toolbar')
    this.keydownListener = e => {
      if (e.which === 90 && e.ctrlKey) { // ctrl + z
        this.undo()
      } else if (e.which === 89 && e.ctrlKey) { // ctrl + y
        this.redo()
      } else if (e.which === 83 && e.ctrlKey) { // ctrl + s
        this.showSaveDialog()
        e.stopPropagation()
        e.preventDefault()
      } else if (e.which === 46) { // delete key
        if (document.querySelectorAll('[contenteditable=true]').length === 0) {
          this.handleClearCellsContent()
        }
      } else if (e.which === 112) { // f1 key
        this.helpDialog.visible = true
        e.stopPropagation()
        e.preventDefault()
      }
    }
    document.body.addEventListener('keydown', this.keydownListener, false)
  },
  beforeDestroy: function () {
    document.body.removeEventListener('keydown', this.keydownListener)
  }
}
</script>

<style scoped id="element-ui-style">
  .fe-toolbar-buttons .el-select--mini {
    width: 102px !important;
  }
  .el-input-number--mini {
    width: auto;
    line-height: 26px;
  }

  .el-autocomplete-suggestion li {
    padding: 0 20px;
    margin: 0;
    line-height: 24px;
    cursor: pointer;
    color: #606266;
    font-size: 14px;
    list-style: none;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

</style>

<style scoped lang="scss">
  .fe-toolbar-preview tr td {
    width: 75px;
    height: 50px;
  }
  .fe-toolbar-preview-top-width {
    border-top-width: 1px;
  }
  .fe-toolbar-preview-horizon-width tr:first-child td {
    border-bottom-width: 1px;
  }
  .fe-toolbar-preview-bottom-width {
    border-bottom-width: 1px;
  }
  .fe-toolbar-preview-left-width {
    border-left-width: 1px;
  }
  .fe-toolbar-preview-vertical-width tr td:first-child {
    border-right-width: 1px;
  }
  .fe-toolbar-preview-right-width {
    border-right-width: 1px;
  }
  .fe-toolbar-buttons {
    font-size: 12px;
    background: #c8d8ed;
  }
  .fe-toolbar-buttons .fe-toolbar-buttons-left {
    float: left;
  }
  .fe-toolbar-buttons .fe-toolbar-buttons-right {
    text-align: right;
  }
  .fe-toolbar-buttons-border-property-dialog-font-mini {
    font-size: 12px;
  }
  .el-button--mini, .el-button--mini.is-round {
    padding: 5px 8px;
  }
  .fe-toolbar-buttons-border-property-dialog-fieldset {
    border: solid 1px #ddd;
  }
  .fe-toolbar-buttons-border-property-dialog-title {
    font-size: 14px;
  }
  .fe-toolbar-block {
    float: left;
    padding: 0px 2px 4px 2px;
    height: 60px;
    line-height: 30px;
  }
  .fe-toolbar-block2 {
    padding: 4px 2px 4px 2px;
    height: 60px;
    text-align: right;
    white {
      color: #ffffff;
    }
  }
  .vue-swatches {
    position: relative;
    outline: none;
    float: left;
  }
  .fe-dialog-title {
    font-size: 16px;
  }

  .whole-size {
    width: 99% !important;
    margin: 2px;
  }
  .refer-name {
    font-size: 12px;
    color: #b4b4b4;
    padding-bottom: 8px;
  }

  /*.el-col {*/
    /*height: 28px;*/
    /*line-height: 28px;*/
  /*}*/
</style>

<style lang="scss">
  .el-autocomplete {
    width: 100%;
    .my-autocomplete {
      li {
        padding: 0 20px 7px 20px;
        line-height: 24px !important;
      }
    }
  }
</style>



