import axios from 'axios'
import md5 from 'md5'
import cookie from 'js-cookie'

import { Loading, MessageBox } from 'element-ui'
import { tenantId } from './param'

// const baseURL = 'http://127.0.0.1:8900/'
const baseURL = '//' + window.location.host

let http = axios.create({
  baseURL: baseURL
})

const tempNonce = []

const tokenKey = 'token'
const refreshTokenKey = 'refreshToken'

http.interceptors.request.use(function (config) {
  let token = getToken()
  if (config.method !== 'get') {
    const timestamp = Math.floor(new Date().getTime())
    const nonce = md5(JSON.stringify(config.data) + config.url + timestamp)
    config.headers.common['timestamp'] = timestamp
    config.headers.common['nonce'] = nonce
    if (tempNonce.indexOf(nonce) !== -1) {
      // eslint-disable-next-line
      return Promise.reject({
        data: {
          code: 809,
          url: config.url,
          data: config.data,
          msg: ''
        }
      })
    } else {
      tempNonce.push(nonce)
    }
  }

  if (token) {
    config.headers.common['Authorization'] = token
  }
  return config
})

let tokenRefreshing = false

// 
http.interceptors.response.use(function (response) {
  // if (tempNonce.indexOf(response.config.nonce) !== -1) {
  //   tempNonce.splice(tempNonce.indexOf(response.config.nonce), 1)
  // }
  // 
  const data = response.data
  if (data) {
    if (data.code) {
      switch (data.code) {
        case 901:
        case 903:
        case -15001:
          if (!tokenRefreshing) {
            tokenRefreshing = true

            const loading = Loading.service({
              fullscreen: true,
              text: 'TokenToken'
            })
            refreshToken(cookie.get(refreshTokenKey), () => {
              loading.close()
            }).then((token) => {
              tokenRefreshing = false
              if (token) {
                onAccessTokenFetched()
              } else {
                MessageBox.confirm('Token', '', {
                  confirmButtonText: '',
                  cancelButtonText: ''
                }).then(() => {
                  window.close()
                }).catch(() => {
                  window.close()
                })
              }
            }).catch(() => {
              tokenRefreshing = false
            })
          }

          const reResponse = new Promise(resolve => {
            addSubscriber(() => {
              resolve(http.request(response.config))
            })
          })
          return reResponse
          // alert(data.msg)
          // MessageBox.alert(data.msg + '', '', {
          //   confirmButtonText: '',
          //   callback: () => {
          //     const loading = Loading.service({
          //       fullscreen: true,
          //       text: '...'
          //     })
          //     let config = {
          //       headers: {
          //         common: {
          //           timestamp: new Date().getTime(),
          //           nonce: Math.random()
          //         }
          //       }
          //     }
          //     http.put('/api/auth/refresh', 'refresh_token=' + cookie.get(refreshTokenKey), config).then(
          //       response => {
          //         loading.close()
          //         const backInfo = response.data
          //         if (backInfo.flag) {
          //           cookie.set(tokenKey, backInfo.data.token, 1 / 24)
          //           cookie.set(refreshTokenKey, backInfo.data.refreshToken, 1)
          //         }
          //       })
          //   }
          // })
          // break
        default:
          break
      }
      return data
    }
    // if (data.flag === false) {
    //   // 
    //   if (data.code !== 802 && data.code !== 809) {
    //     alert(data.msg)
    //   } else {
    //   }
    //   return data
    // }
  }
  return data
}, function (error) {
  console.error('axios error', error)
  if (!error.response) {
    return Promise.reject(error)
  }
  return Promise.reject(error.response.data, error.response)
})

let subscribers = []
function onAccessTokenFetched () {
  subscribers.forEach((callback) => {
    callback()
  })
  subscribers = []
}

function addSubscriber (callback) {
  subscribers.push(callback)
}

function refreshToken (refreshToken, callback) {
  return new Promise(resolve => {
    let instance = axios.create({
      baseURL: process.env.BASE_API
    })
    const timestamp = Math.floor(new Date().getTime() / 1000)
    let config = {
      headers: {
        common: {
          timestamp: timestamp,
          nonce: Math.random()
        }
      }
    }
    instance.put('/api/auth/refresh', 'refresh_token=' + refreshToken, config).then(
      response => {
        const backInfo = response.data
        callback()
        if (backInfo.flag) {
          // commit('SET_TOKEN', backInfo.data.token)
          // commit('SET_REFRESHTOKEN', backInfo.data.refreshToken)

          cookie.set(tokenKey, backInfo.data.token)
          cookie.set(refreshTokenKey, backInfo.data.refreshToken)

          if (tenantId !== backInfo.data.tenantId) {
            MessageBox.confirm('', '', {
              confirmButtonText: '',
              cancelButtonText: ''
            }).then(() => {
              window.close()
            }).catch(() => {
              window.close()
            })
            // location.reload()
          } else {
            setTimeout(() => {
              resolve(backInfo.data.token)
            }, 100)
          }
        } else {
          resolve(null)
        }
      })
  })
}

function getToken () {
  return cookie.get(tokenKey)
}

export {http}



