<template draggable="false" ondragstart="javascript: return false;">
  <div draggable="false" ondragstart="javascript: return false;" v-loading="contentLoading" element-loading-text="">
    <FormEditor @loading="switchLoading"/>
    <ContentMenu/>
  </div>
</template>

<script>
import { http } from './util/http'
import axios from 'axios'
import { formKey, formId, tenantId } from './util/param'

import FormEditor from './components/FormEditor'
import ContentMenu from './components/ContentMenu'
import { mapActions } from 'vuex'

const version = '10.1.16.1'

export default {
  name: 'app',
  components: {
    FormEditor,
    ContentMenu
  },
  computed: {
    type () {
      return this.$store.state.cache.type
    },
    ueditorConfig () {
      return this.$store.state.config.ueditorConfig
    }
  },
  data: function () {
    return {
      contentLoading: false
    }
  },
  methods: {
    saveUEContent () {
      let source = this.$refs['ueditor'].getUEContent()
      this.endUEdit({source})
    },
    switchLoading () {
      this.contentLoading = !this.contentLoading
    },
    getForm (refreshToken) {
      let _this = this

      if (formId) {
        let loading = this.$loading({
          fullscreen: true,
          text: '...'
        })

        http.post('api/field/form/getForm', {id: formId, formKey: formKey})
          .then(function (response) {
            if (response.data === null) {
              _this.$message.error('')
              return
            }

            if (formKey !== response.data.formKey) {
              _this.$message.error('')
              return
            }

            loading.close()
            let form = response.data
            let json = JSON.parse(form.json)
            json.form = form

            _this.$store.replaceState(Object.assign({}, _this.$store.state, json))
            setTimeout(() => {
              _this.recordState()
            }, 100)
          })
          .catch(function (error) {
            console.log(error)
            loading.close()
          })
      }
    },
    getFormType (refreshToken) {
      let _this = this

      http.post('api/field/formType/list', {refreshToken, targetTenantId: tenantId})
        .then(function (response) {
          let formTypeList = response.data.list
          _this.$store.replaceState(Object.assign({}, _this.$store.state, {formTypeList}))
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    getReferWindows (refreshToken) {
      let _this_ = this

      http.get('api/field/referWindow/all', {refreshToken})
        .then(function (response) {
          let referWindowList = response.data
          _this_.$store.replaceState(Object.assign({}, _this_.$store.state, {referWindowList}))
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    getCodes (refreshToken) {
      let _this_ = this

      http.get('api/field/code/all', {refreshToken})
        .then(function (response) {
          let codeList = response.data
          _this_.$store.replaceState(Object.assign({}, _this_.$store.state, {codeList}))
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    async (refreshToken) {
      return new Promise((resolve, reject) => {
        this.getForm(refreshToken)
      })()
    },
    ...mapActions([
      'recordInitState',
      'setCacheType',
      'setForm',
      'setCopyCellArea',
      'recordState'
    ])
  },
  mounted: function () {
    // console.log(`FORM EDITOR VERSION: %c ${version}`, 'background: #ffffff; color: #bada55')
    console.log(`FORM EDITOR VERSION: %c ${version}`, 'color: #bada55')
    this.recordInitState()

    if (process.env.NODE_ENV === 'production') {
      let windowName = window.name
      let refreshToken = windowName.split('|')[0]
      // let _this_ = this
      // this.getFormType(refreshToken)
      // this.getForm(refreshToken)
      // this.getReferWindows(refreshToken)
      // this.getCodes(refreshToken)
      axios.all([this.getFormType(refreshToken), this.getForm(refreshToken), this.getReferWindows(refreshToken), this.getCodes(refreshToken)])
        .then(axios.spread(function (acct, perms) {
        }))
    } else {
    }
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    width: 100%;
  }

</style>



